import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

//  key
const STATE_KEY = 'state'
// , storelocalStorage
const stateLocalstorePlugin = function (store) {
    // store , store
    let state = Lockr.get(STATE_KEY)

    if(state) {
        store.commit('setUser', state.user || null)
    }

    store.subscribe((mutation, state) => {
        //  mutation 
        // mutation  { type, payload }
        Lockr.set(STATE_KEY, state)
    })
};


export default new Vuex.Store({
    strict: process.env.NODE_ENV !== 'production',
    state: {
        projectName: '',
        systemName: '',
        user: null,
        isLoading: false
    },
    mutations: {
        updateLoadingStatus(state, payload) {
            state.isLoading = payload.isLoading
        },
        setUser(state, user) {
            state.user = user;
        },
    },
    actions: {
        clearUserInfo(context) {
            Lockr.rm('userInfo')
            //token
            Lockr.rm('token')
            context.commit('setUser', null)
        },
        storeUserInfo(context, { userInfo, token }) {
            Lockr.set('userInfo', userInfo)
            Lockr.set('token', token)
            context.commit('setUser', userInfo)
        },
    },
    plugins: [
        stateLocalstorePlugin
    ]
});
export {
    STATE_KEY
}


