
import { mapState } from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
data() {
/**
* form       
* mobile     
* verifyCode 
* showLogin  
* codeLock   
* countDown  
* interval   
*/
return {
form: {
mobile: '',
verifyCode: '',
},
showLogin: false,
codeLock: false,
countDown: 60,
interval: null
}
},
components: {
Group,
XInput,
XButton
},
computed: {
...mapState([
'projectName',
'systemName',
'user'
])
},
methods: {
/**
* 
*/
relocation() {

if (this.$route.query && this.$route.query._from) {
router.push(this.$route.query._from);
} else {
router.push('/');
}
},
/**
* 60
*/
sendCode() {
const that = this
const mobile = that.form.mobile.replace(/\s/g, '')


if(mobile != '') {
api.post('/sms/verify_code', {mobile: mobile}).then(data => {

that.codeLock = true
that.countDown = 60
that.interval = setInterval(() => {

if(that.countDown == 0) {
that.codeLock = false
clearInterval(that.interval)
return
}

that.countDown = that.countDown - 1
}, 1000)
}).catch((msg) => {}).finally((msg) => {})
} else {
that.$vux.toast.show({
text: '',
type: 'warn'
})
}
},
/**
* 
*/
doLogin() {
const that = this


const param = {
mobile: that.form.mobile.replace(/\s/g, ''),
verify_code: that.form.verifyCode.replace(/\s/g, '')
}

if(param.mobile != '' && param.verify_code != '') {

api.post('/login', param).then(data => {
store.dispatch('storeUserInfo', data)
that.relocation()
}).catch((msg) => {}).finally((msg) => {})
}
}
},
created: function () {
if(this.user) {
this.$vux.toast.show({
text: '',
type: 'warn'
})
router.go(-1)
return
}


document.title = '/'
},
mounted () {

const that = this;
that.showLogin = true;
}
}



