import axios from 'axios'
import AlertModule from 'vux/src/plugins/alert/module'


/**
* https://o2o.niucha.ren/api/app/user/
* https://xiaochengxu.niucha.ren/api/app/user/
*/

window.baseApiUrl = 'https://o2o.niucha.ren/api/app/user/'
const CODE_OK = 0
const CODE_UN_LOGIN = 10003

class ResponseError {
constructor(response) {
this.response = response
}
}

/**
* 
*
* 1. 0
* 2. promise
*/
function handlerRes(res) {
if (res && res.code === CODE_OK) {
return res.data
} else {
return Promise.reject(new ResponseError(res))
}
}

/**
* 
*
* 1. ResponseError
*  1.1 
*     1.1.1 10003
*     1.1.2 
*  1.2 
* 2. 
*/
function handlerError(error) {
if(error instanceof ResponseError) {
let res = error.response

if(res && res.code) {
switch (res.code) {
case CODE_UN_LOGIN:
Lockr.rm('userInfo')
Lockr.rm('menus')
router.push('/login')
AlertModule.show({
content: ', '
})
break
default:
console.log(':', res)
if (!res.disableErrorMessage) {
AlertModule.show({
content: res.message
})
}
break
}
} else {
console.log(':', res)
}
} else {
console.error('network error: ', error)
AlertModule.show({
content: res.message
})
}
}

/**
* 
*
* 1. api
* 2. //api
* 3. apiurl
*/
function getRealUrl(url) {
if(url.indexOf(window.baseApiUrl) === 0){
return url
}
if(url.indexOf('/') === 0) {
url = url.substr(1)
}
return window.baseApiUrl + url
}

/**
* get
*
* param
*  url
*  params
*  defaultHandlerRes
*
* 1. XMLHttpRequesttoken
* 2. axioshandlerRes
* 3. axiospromisepromisehandlerError
* 4. promise
*/
function get(url, params, defaultHandlerRes=true) {
let options = {
headers: {'X-Requested-With': 'XMLHttpRequest', 'token': Lockr.get('token') },
params: params,
}
url = getRealUrl(url)
let promise = axios.get(url, options).then(res => {
let result = res.data
if(defaultHandlerRes) {
return handlerRes(result)
} else {
return result;
}
});
promise.catch(handlerError)
return promise
}

/**
* get
*/
function post(url, params, defaultHandlerRes=true) {
let options = {
headers: { 'X-Requested-With': 'XMLHttpRequest', 'token': Lockr.get('token') }
}
url = getRealUrl(url)
let promise = axios.post(url, params, options).then(res => {
let result = res.data

if(defaultHandlerRes) {
return handlerRes(result)
} else {
return result
}
});
promise.catch(handlerError)
return promise
}

/**
* promisepromise
*/
function mockData(data) {
return new Promise((resolve, reject) => {
resolve(data)
})
}

export default {
get,
post,
mockData,
handlerRes,
}


